body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  padding-bottom: 2em;
  position: absolute;
  min-width: 550px;
  width: 100%;
  height: 100vh;
  background-color: #f1eae8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#bgWrapper {
  background-image: url("../public/bg.jpg");
  background-size: cover;
  background-attachment: fixed;
  border-block: #2c2220 solid 5px;
}

.header {
  width: fit-content;
  height: fit-content;
  margin: none;
  display: flex;
  flex-direction: column;
  margin: auto;
  padding-block: 10px;
}

.header h1 {
  text-align: center;
  color: white;
  font-size: 80px;
  margin: auto;
  text-shadow: 0 0 4px black;
}

.headerImg {
  width: 200px;
  height: 200px;
  margin: auto;
  background-image: url("../public/kyler.jpg");
  background-size: 100%;
  background-position: center top;
  background-repeat: no-repeat;
  box-shadow: 0 0 3px white inset, 0 0 3px black;
  border: white solid 2px;
  border-radius: 50%;
  padding: 10px;
  transition: 2s;
}

#topLeft {
  position: absolute;
  color: #302422;
  font-size: x-large;
  top: -20px;
  left: 10px;
}

#topLeft:hover {
  cursor: pointer;
}

.navbar {
  background: #f1eae8;
  box-shadow: 0 0 4px #2c2220;
  height: 3em;
  top: 0;
  position: sticky;
  display: flex;
  flex-direction: row;
}

.navbar .nav {
  display: flex;
  flex-direction: row;
  margin: auto;
  height: 3em;
  width: fit-content;
}

a h3 {
  margin: none;
  color: transparent;
  text-shadow: 0 0 0 #2c2220;
  transform: translateY(-2px);
}


header .link {
  font-weight: 900;
  text-decoration: none;
  color: #2c2220;
  height: 2.5em;
  width: 2.5em;
  margin-block: auto;
  text-align: center;
  margin: auto;
  margin-inline: 5px;
  border-radius: 50%;
  transition: 500ms;
  display: grid;
  align-content: center;
}

header a.active, .link.active:hover {
  background: #2c222088;
  transition: 500ms;
  text-align: center;
  border-radius: 50%;
}

header .link:hover {
  background: #2c222088;
  transition: 500ms;
  border-radius: 5px;
}

.contact {
  padding-top: 2rem;
  padding-bottom: 50px;
  padding-inline: 5px;
  max-width: 640px;
  margin: auto;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.col-span-2 {
  grid-column: span 2;
  margin: auto;
}

.centerText {
  text-align: center;
}

.contact p {
  padding: 10px;
  padding-block: 15px;
}

.contact a {
  text-decoration: none;
  padding: 5px;
  color: #f1eae8;
  border-radius: 5px;
  background-color: #2c2220;
}

#aboutme {
  padding-bottom: 2rem;
}

.content {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top: 2rem;
  padding-bottom: 50px;
  padding-inline: 5px;
}

.projectPage {
  gap: 2rem;
}

.projectCard {
  margin: auto;
  width: 90%;
  padding: 10px;
  border: 1px dashed #2c2220;
  text-align: center;
}

.content a {
  color: #2c2220;
}

.content h3 {
  text-align: center;
}

.contentImg {
  width: auto;
  height: 100px;
  margin: auto;
  padding: 25px;
}

p {
  background-color: #f1eae8;
  border-radius: 5px;
  border: 1px dashed #2c2220;
  padding-block: 2em;
  padding-inline: 2em;
  margin: auto;
}

.projectCard {
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 1rem;
}

.cardProfile {
  display: grid;
  padding: 1rem;
  border: #2c2220 solid 1px;
}

.cardImg {
  width: 100px;
  height: auto;
  border-radius: 50%;
  margin: auto;
}

span {
  font-size: 20px;
  font-weight: bolder;
}

#dogHeader {
  width: fit-content;
  margin: auto;
  margin-top: 50px;
  color: #2c2220;
  grid-column: span 3;
}

.dogWrapper {
  text-align: center;
}

.dogWrapper h2 {
  color: #2c2220;
}

.dog {
  margin: auto;
  width: 96%;
  border-radius: 10px;
  border: solid 3px #2c2220;
}

#dogContainer {
  min-height: fit-content;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column: span 2;
  gap: 10px;
}